.bonusWrapper {
    margin-top: 2px;
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: #bebebe;
}

.bonusItem {
    width: 100%;
    /*border: 1px solid #ccc;*/
    /*border-radius: 15px;*/
    margin-bottom: 10px;
}

.bonusItemTitle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
    font-weight: 600;
    /*border-radius: 5px;*/
}

.bonusItemContent {
    padding: 16px;
    /*background-color: #f9f9f9;*/
    min-height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    background-size: cover;
}

.bonusItemContentDark {

    width: 100%;
    height: 400px;
    background-color: black;
    opacity: 0.8;
    border-radius: 10px;
}

.imageWrapper {
    display: flex;
    align-items: center;
    justify-content: space-around;

}

.boxItem {
    width: 80px;
    height: 80px;
    border: 1px solid black
}

.arrowIcon {
    font-size: 24px;
    margin-left: 8px;
}

.buttons {
    margin: 10px 15px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 90%;
    height: 40px;
    color: white;
    font-weight: bold;
    border: 1px solid white;
    border-radius: 10px;
}

.buttons img {
    max-width: 30px;
}

.title {
    width: 100%;
    height: 20px;
    color: whitesmoke;
    font-weight: bold;
    margin-top: 10px;

}

.selectedImage {
    margin-top: 20px;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border: 3px solid green;
}

.description {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 70px;
    width: 97%;
    margin: 35px 5px 0 5px;
    border-radius: 10px;
    background-color: #656363;
    color: white;
    font-weight: bolder;
    font-size: 20px;
}
.metelli {
    background-color: #fecc00;
    color: #00498d;
    font-weight: bolder;
    text-transform: uppercase;
}

.default {
    background-color: #bebebe;
    color: black;
}

.lynx {
    background-color: #929497;
    color: black;
}
.mann {
    background-color: #fff001;
    color: #017c51;
    font-weight: bolder;
    text-transform: uppercase;
}
.asMetall{
    background-color: #f15922;
    color: black;
    font-weight: bolder;
}
.bluePrint{
    background-color: #b3d5e9;
    color: #031c97;
    font-weight: bolder;

}